@include tablet{
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog{
    margin-bottom: 2.5rem;
    
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type{
        margin-bottom: 0;
    }
}

.informacion-meta{
    span{
        color: $azul;
        font-weight: 500;
    }
}

.texto-entrada{
    a{
        color: $negro;
        display: block;
        margin-top: 2rem;

        @include tablet{
            margin-top: 0;
        }
    }

    h4{
        margin: 0;
        line-height: 1.3;

        &::after{
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
}

.blog, .testimoniales{
    h3{
        font-weight: 500;
        margin-bottom: 4rem;
    }
}

