.no-webp .inicio{
    background-image: url('../img/header.jpg');
}

.webp .inicio{
    background-image: url('../img/header.webp');
} 

.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center;
        background-size: cover;

        @include tablet{
            min-height: 100vh;
        }

        .contenido-header{
            @include tablet{    
                min-height: 95vh;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1{
        display: none;
    }

    @include tablet{
        h1{
            display: block;
            text-align: left;
            color: $blanco;
            font-weight: 500;
            line-height: 2;
            max-width: 80rem;
        }
    }
    
}